body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header{
  display: flex;
  align-items: center;
  height: 65px;
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
  padding: 20px;
  color: white;
}
.logo{
  height: 100%;
}
.header--title{
  font-size: 1.25rem;
  margin-right: auto;
}
.header--project{
  font-size: 0.75rem;
  font-weight: 500;
}

.form{
  display: grid;
  grid-template:40px 40px / 1fr 1fr ;
  gap:17px;
}
.form--button{
  grid-column: 1/-1;
  font-family: 'Karla',sans-serif;
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
  color: white;
  border-radius: 5px;
}
.form--input{
  font-family: 'Karla',sans-serif;
  border-radius: 5px;
  border:1px solid;
  text-indent: 5px ;
  
}
main{
  padding: 36px;
}

.meme{
  
  margin-top: 10px;
  position: relative;

}

.meme--text{
  position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
  
}

.bottom {
  bottom: 0;
}

.top {
  top: 0;
}